import DeviceOrientation from 'react-device-orientation';
import './App.css';

interface DeviceOrientationData {
  absolute: boolean;
  alpha: number | null;
  beta: number | null;
  gamma: number | null;
}

const App = () => (
  <DeviceOrientation>
    {({ absolute, alpha, beta, gamma }: DeviceOrientationData) => (
      <div>
        {`Absolute: ${absolute}`}
        {`Alpha: ${alpha}`}
        {`Beta: ${beta}`}
        {`Gamma: ${gamma}`}
      </div>
    )}
  </DeviceOrientation>
);

export default App;
